import React, { useState, useEffect } from 'react';
import './App.css';
import Biodata from './komponen/Biodata';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import header from './header.JPG';
import logo from './Peteng2.png';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Snackbar from '@material-ui/core/Snackbar';
import CloseIcon from '@material-ui/icons/Close';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import {config} from './config/firebase';

const API_KEY = config['apikey'];
const databaseURL = config['databaseURL']


export default function Jumboo() {
  const [user, setUser] = useState({});

  useEffect(() => {
    const urlFetch = fetch('https://repository-1d687.firebaseio.com//user/abcd.json')

    urlFetch.then(res => {
      console.log('Response', res);

      if( res.status === 200)
        return res.json()
    }).then( resJson => {
      setUser(resJson)
    })
  }, [])

		return(
			<div className="animated fadeIn">
				<img src={logo} className="App-profil" alt="logo" />
				<a className="App-nama">{user.nama}</a>
        <Link to ='/editProfile'><Button variant="contained" size="small">Setting</Button> </Link>
				<DisabledTabs  />
        <main>
        <Switch>
        <Route path='/postAkun' exact component={PostAkun} />
         <Route path='/biodata' exact component={Biodata} />
         <Route path='/penelitian' exact component={Penelitiann} />
         <Route path='/pengabdian' exact component={Pengabdiann} />
         <Route path='/' component={Penelitiann} />
        </Switch>
        </main>

			</div>

		)
	}


function DisabledTabs() {
  const [value, setValue] = React.useState(2);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <Tabs
      value={value}
      indicatorColor="primary"
      textColor="primary"
      onChange={handleChange}
      aria-label="disabled tabs example"
      >
    			<Link to='/postAkun'>	<Tab label="Post" /> </Link>
          <Link to='/biodata'> <Tab label="Profil" /> </Link>
          <Link to='/penelitian'>  <Tab label="Peneletian" /> </Link>
          <Link to='/pengabdian'> <Tab label="Pengabdian" /> </Link>
      </Tabs>
    </Paper>
  );
}

function PostAkun(){
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(false);


  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClosed = () => {
    setOpen(false);
  };

  return (
    <div>
      <Penelitian name="Alvin" judul="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website"/>
      <Link to='/post'>
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
      <Link to="/editPosting">
       <MenuItem onClick={handleClose}>Edit</MenuItem>
       </Link>

      </Menu>
      <br/>
      <br/>
      <Pengabdian name="nanda" judul="Rancang Bangun Sistem Informasi Manajemen Kelurahan (Studi Kasus di Kelurahan Kauman dan Kasin Kota Malang)"/>
      <Link to='/post'>
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to='/editPosting'>
        <MenuItem onClick={handleClose}>Edit</MenuItem>
        </Link>
        <MenuItem onClick={handleClickOpen}>Hapus</MenuItem>
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Use Google's location service?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Apakah anda yakin ingin menghapus postingan?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClosed} color="primary">
            Tidak
          </Button>
          <Button onClick={handleClosed} color="primary" autoFocus>
            Ya
          </Button>
        </DialogActions>
      </Dialog>
      </Menu>
    </div>
  );
}

function Pengabdiann(){
  return(
    <div>
    <Pengabdian name="nanda" judul="Rancang Bangun Sistem Informasi Manajemen Kelurahan (Studi Kasus di Kelurahan Kauman dan Kasin Kota Malang)"/>
    </div>
  );
}

function Penelitiann(){
  return(
    <div>
      <Penelitian name="Alvin" judul="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website"/>
    </div>
  );
}
